.buttons {
    display: flex;
    flex-direction: column;
    gap: 20px; /* Increased gap to simulate Game Boy layout */
    align-items: center;
    margin-top: 20px; /* Added margin to better position the buttons */
  }
  
  .button {
    width: 40px;
    height: 40px;
    background-color: #333;
    border-radius: 50%;
    border: 2px solid #000; /* Added border for a more distinct look */
    position: relative;
  }
  
  .button-a::before {
    content: 'A';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 1.2em;
    font-weight: bold;
  }
  
  .button-b::before {
    content: 'B';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
    font-size: 1.2em;
    font-weight: bold;
  }
  