.screen {
    background-color: #ccc;
    border: 5px solid #333;
    border-radius: 5px;
    padding: 10px;
    margin-top: 20px;
  }
  
  .game-screen {
    position: relative;
    width: 100%;
    height: 230px;
    background-color: #fff;
    border: 3px solid #666;
  }
  
  .battery-icon {
    position: absolute;
    top: 5px;
    right: 5px;
    width: 30px;
    height: 15px;
    border: 2px solid #333;
  }
  
  .skull-icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50px;
    height: 50px;
    background-size: contain;
    background-repeat: no-repeat;
  }
  
  .progress-bar {
    position: absolute;
    bottom: 10px;
    left: 10px;
    width: 90%;
    height: 10px;
    background-color: #ccc;
    border: 1px solid #333;
  }
  
  .progress {
    width: 50%;
    height: 100%;
    background-color: #000;
  }
  